<template>
  <div class="top-button-container" v-show="show" @click="handleClick">
      TOP
  </div>
</template>

<script>
export default {
    data() {
        return{
            show:false
        }
    },
    methods:{
        handleClick() {
            this.$bus.$emit("setMainScroll",0)
        },
        handleScroll(dom) {
            if(!dom) {
                this.show = false;
                return
            }
            if (dom.scrollTop >= 500) {
                this.show = true;
            }else{
                this.show = false;
            }

        }
    },
    created() {
        this.$bus.$on("mainScroll",this.handleScroll)
    },
    destroyed() {
        this.$bus.$off("mainScroll",this.handleScroll)
    }
}
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.top-button-container {
  background: @primary;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: fixed;
  z-index: 99;
  right: 50px;
  bottom: 50px;
  cursor: pointer;
  line-height: 50px;
  color: #fff;
  text-align: center;
}
</style>