import eventBus from '@/eventBus.js'
import defaultImg from "@/assets/default.gif"
import debounce from "@/utils/debounce.js";
let imgs = [];


function setImage (item) {
    item.dom.src = defaultImg;

    let rect = item.dom.getBoundingClientRect();
    let top = rect.top;
        let height = rect.height || 150;
        let clientHeight = document.documentElement.clientHeight;
        if (top >= -height && top <= clientHeight) {
            item.dom.src = item.src;
            imgs = imgs.filter((i) => i !== item)
        }

}
function setImages() {
    for (let i of imgs) {
        setImage(i)
     
    }
}
function handleScroll() {
    setImages();
  }
eventBus.$on("mainScroll",debounce(handleScroll,50));


export default {
    inserted (el,bindings) {
        const img = {
            dom: el,
            src: bindings.value,
          };
          imgs.push(img);
          // 立即处理它
          setImage(img);
        },
        unbind(el) {
            imgs = imgs.filter((img) => img.dom !== el);
          },
}


