import getComponentRootDom from "./getComponentRootDom";
import Icon from '@/components/icon';
import styles from './showMessage.module.less';
export default function (option) {
    //得到的iconDom是元素节点，不是html结构，要用outerHtml
    let content = option.content || '';
    let container = option.container || document.body;
    let type = option.type || 'info';
    let duration = option.duration ||2000;
    let iconDom = getComponentRootDom(Icon,{ type })
    let div = document.createElement('div');
    let typeName = 'message-'+type;
    div.innerHTML = `<span class='${styles.icon}'>${iconDom.outerHTML}</span><div>${content}</div>`;
    div.className =`${styles.message} ${styles[typeName]}`;
  if(option.container) {
      
      if (getComputedStyle(container).position == 'static') {
        //container.style.position读写的是行间样式
        container.style.position = 'relative';
    }
  }
    container.appendChild(div)
      // 浏览器强行渲染
    div.clientHeight;
    div.style.opacity = 1;
    div.style.transform = `translate(-50%, -50%)`;
    setTimeout(() => {
        div.style.opacity = 0;
        div.style.transform = `translate(-50%, -50%) translateY(-25px)`;
        div.addEventListener(
          "transitionend",
          function() {
            div.remove();
            // 运行回调函数
            option.callback && option.callback();
          },
          { once: true }
        );
      }, duration);
}