

let routerTitle = "";
let siteTitle = "";

function setTitle () {
    if (!routerTitle && !siteTitle){
        return
    }else if (!routerTitle && siteTitle) {
        document.title = siteTitle;
    }else if(routerTitle && !siteTitle) {
        document.title = routerTitle;
    }else{
        document.title = `${routerTitle}-${siteTitle}`;
    }
}
function storeControl (title) {
    siteTitle = title;
    setTitle ()
}

function routerControl (title) {
    routerTitle = title;
    setTitle ()
}

export default {
    storeControl,
    routerControl
}