import NotFound from "@/components/NotFound.vue"
import "Nprogress/nprogress.css";
import {start, done, configure} from "Nprogress";
configure({
  trickleSpeed:20,
})
const routes ={
  routes:[
    {name:'Home',path:'/',component:async ()=>{
     start()
      const Home = await import('@/views/home')
     done()
      return Home
    },meta:{
      title:"首页"
    }},
    {name:'About',path:'/about',component:async ()=>{
     start()
      const About = await import('@/views/about')
     done()
      return About
    },meta:{
      title:"关于我"
    }},
    {name:'Blog',path:'/article',component:async ()=>{
     start()
      const Blog = await import('@/views/blog')
     done()
      return Blog
    },meta:{
      title:"文章"
    }},
    {name: "CategoryBlog", path: "/article/cate/:categoryId", component:async ()=>{
     start()
      const blog = await import('@/views/blog')
     done()
      return blog
    },meta:{
      title:"文章"
    } },
    {name: "BlogDetail", path: "/article/:id", component:async ()=>{
     start()
      const BlogDetail = await import('@/views/blog/detail.vue')
     done()
      return BlogDetail
    },meta:{
      title:"文章详情"
    } },
    {name:'Project',path:'/project',component:async ()=>{
     start()
      const Project = await import('@/views/project')
     done()
      return Project
    },meta:{
      title:"项目效果"
    }},
    {name:'Message',path:'/message',component:async ()=>{
     start()
      const Message = await import('@/views/message')
     done()
      return Message
    },meta:{
      title:"留言板"
    }},{
      name:"NotFound",
      path:"*",
      component:NotFound
    }
  ],
  mode:'history'
}
export default routes